
$color1: #d1dd19e6;
$color1transparent: #d1dd1999;
$dark: #5f5302;

html, body {
  height: 100%;
  width: 100%;
}
body {
  background: url('/img/risotto-de-ceps.jpg') center no-repeat;
  background-size: cover;
  background-attachment: fixed;
}
nav {
  background: rgba(209, 221, 25, 0.6);
  background: $color1transparent;
  position: fixed;
  top: 0px;
  z-index: 999;
}
.template {
  display: none;
}

.dropdown-nested {
    overflow-y: visible;
    width: auto;
    min-width: 345px;
    background: rgba(209, 221, 25, 0.95);
    background: $color1;
}
.dropdown-content .dropdown-content {
   /* left: 100%;*/ /*drop hacia la izquierda*/
   left: -100%; /*drop hacia la derecha*/
   /* left: auto;*/ /*drop hacia el centro*/
}
.container {
    background: #eee;
    border-radius: 8px;
}
:focus {
  outline: none;
}
section.container {
  margin-top: 100px;
  padding: 40px;
}
body.home section.container {
  height: 218px;
  padding: 0;
}
section#content .col p {
  padding: 0;
  margin: 12px 0;
}

nav ul>li:hover, nav ul>li a:focus {
  background: $color1;
  opacity: 1;
}

nav ul a, a.brand-logo, .dropdown-content.dropdown-nested li a {
  color: $dark;
  font-weight: bold;
  height: 100%;
}
nav img {
  max-height: 100%;
}
nav ul>li {
  position: relative;
  top: 0;
  transition: top 100ms;
  transition-timing-function: ease-out;
}
nav ul>li.loading {
  top: -64px;
}
.dropdown-nested li {
  height: 35px;
  min-height: 35px;
}
.dropdown-nested li a {
  white-space: nowrap;
  font-size: .8em;
  line-height: .8em;
  text-transform: uppercase;
}
#recipe-list {
  text-align: center;
}
#recipe-list h4 {
  float: center;
  border-bottom: 2px solid black;
  padding: 10px 20px;
  display: inline-block;
  background: #eee;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}
section .recipe-ingredients ul, section .recipe-description ol {
  padding-left: 20px;
}
section .recipe-ingredients ul li {
  list-style-type: square;
}
body #recipe-list a .recipe-thumb .card {
  min-height: 190px;
}
h6 {font-weight: bold;}
.recipe-category {
  background: $dark;
  color: $color1;
  padding: 4px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}
